import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import {SocketService} from './shares/socket.service';
import { LoginComponent } from './login/login.component';
import {LiveDataComponentComponent} from './live/live-data-component.component'
import {SetPointComponent}from './set-point/set-point.component';

import {DataService} from './shares/data.service'
import {UserService} from './shares/user.service'
import {AppRoutingModule} from './app-routing/app-routing.module'

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    LiveDataComponentComponent,
    SetPointComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule
  ],
  providers: [SocketService, DataService,UserService],
  bootstrap: [AppComponent]
})
export class AppModule { }



