import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { LiveDataComponentComponent } from '../live/live-data-component.component'
import { SetPointComponent } from '../set-point/set-point.component'
import {LoginComponent} from '../login/login.component'

const routes: Routes= [
	{ path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: 'login',  component: LoginComponent },
	{ path: 'live',  component: LiveDataComponentComponent },
	{ path: 'setting',  component: SetPointComponent }
]

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [
  	RouterModule
  ]
})
export class AppRoutingModule { }



